const actions = {
  getDebitMemos: (page, search, filter) => ({
    type: 'GET_DEBIT_MEMOS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createDebitMemo: () => ({
    type: 'CREATE_DEBIT_MEMO_REQUEST'
  }),
  storeDebitMemo: data => ({
    type: 'STORE_DEBIT_MEMO_REQUEST',
    payload: { body: data }
  }),
  editDebitMemo: id_debit_memo => ({
    type: 'EDIT_DEBIT_MEMO_REQUEST',
    payload: { id_debit_memo: id_debit_memo }
  }),
  updateDebitMemo: (id_debit_memo, data) => ({
    type: 'UPDATE_DEBIT_MEMO_REQUEST',
    payload: {
      id_debit_memo: id_debit_memo,
      body: data
    }
  }),
  showDebitMemo: id_debit_memo => ({
    type: 'SHOW_DEBIT_MEMO_REQUEST',
    payload: { id_debit_memo: id_debit_memo }
  }),
  processDebitMemo: id_debit_memo => ({
    type: "PROCESS_DEBIT_MEMO_REQUEST",
    payload: { id_debit_memo }
  }),
  cancelDebitMemo: (id_debit_memo, data) => ({
    type: "CANCEL_DEBIT_MEMO_REQUEST",
    payload: { 
      id_debit_memo,
      body : data
    }
  }),
  uploadDocument: (id_debit_memo, data) => ({
    type: "UPLOAD_DEBIT_MEMO_DOCUMENT_REQUEST",
    payload: {
      id_debit_memo,
      body : data
    }
  }),
  downloadDocument: id_debit_memo_document => ({
    type: "DOWNLOAD_DEBIT_MEMO_DOCUMENT_REQUEST",
    payload: {id_debit_memo_document}
  }),
  deleteDocument: id_debit_memo_document => ({
    type: "DELETE_DEBIT_MEMO_DOCUMENT_REQUEST",
    payload: { id_debit_memo_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
